/*
    Author: GRADI KAYAMBA
    width: calc(20% + 100px) -  to calculate some related size to its parent
    colors from dark to light: #190934, #300D6E, #5918C9, #905BEC, #E9DFFB
*/
@charset "utf-8";
:root {
    --main-bg-facto-color-one: #0F0830;
    --main-bg-facto-color-two: #664AE0;
}
*, 
*:before, 
*:after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box; 
}
body { margin:0; }
img { border:0; }

::-webkit-scrollbar { width: 17px; }
::-webkit-scrollbar-button { background-color: #664AE0; border: 2px solid white; }
::-webkit-scrollbar-thumb { background-color: #4A29DB; border: 2px solid white; }
::-webkit-scrollbar-thumb:hover { background-color: #664AE0; border: 3px solid white; }
::-webkit-scrollbar-track { background-color: white; }

.Container,
.row_inner {
    display: flex;
    flex-wrap: wrap;
}

.Box {
    padding: 20px;
    text-align: center;
    flex-basis: 50%;
    /*border: 1px solid white;*/
}
.box_3{
    flex-basis: 33.333%;
}
.box_expand { 
    flex-basis:100%; 

}
.row_float_right { 
    justify-content: flex-end; 

}
.Aside {
    flex-basis: 20%;
    background-color: #f9f8fe;
    position:sticky;
    position:-webkit-sticky;
    top:0;
    overflow:auto;
}
.Main {
    color:black;
}
.Main, 
.Footer {
    flex-basis:80%;
    color:black;

}
.Footer{
    color:white;
    background-color: var(--main-bg-facto-color-one);
}
.Footer section{
    text-align:left;
}
.Footer section:last-child {
    text-align: center;
    border-top: 1px solid #905BEC;
}
