}
.container {
    text-align: center;
}

.Button {
    border: 1px solid var(--main-bg-facto-color-one);
    background-color: #2B1787;
    padding: 5px 10px;
    margin-bottom: 20px;
    color: white;
}
.Button:hover {
    background: var(--main-bg-facto-color-one);
    color: white;
    transition: .5s;
}

.accordion .title {
    padding: 10px 30px;
    cursor: pointer;
    transform: translate3d(0, 0, 0);
    color: white;
    border: 1px solid #f9f8fe;
    border-left: 2px solid #f9f8fe;
    border-right: 2px solid #f9f8fe;
    background: var(--main-bg-facto-color-two);
    text-align: left;
}
.accordion .title::after {
    content: "+";
    color: white;
    transition: transform .5s ease-in-out;
    right: 30px;
}

.accordion .title.is_expanded {
    transition: background .5s;
    background: var(--main-bg-facto-color-one);
}
.accordion .title.is_expanded::after {
    content: "-";
    transform: rotate(-360deg);
}

.content {
    overflow: hidden;
    max-height: 0;
    transition: max-height .5s;


}
.content p {
    padding: 30px 0;
    margin: 0;
    opacity: 0;
    transition: .5s;
}

.content.is_expanded {
    max-height: 500px;
    overflow: hidden;
}
.content.is_expanded p {
    opacity: 1;
}

.content.is_expanded p:after {
    width: 100%;
    height: 10px;
    background: var(--main-bg-facto-color-one);
    content: '';
}
