/*
    Author: GRADI KAYAMBA
    width: calc(20% + 100px) -  to calculate some related size to its parent
*/
@charset "utf-8";
:root {
    --main-bg-color-one: #242428;
    --main-bg-color-two: #ffb416;
}
*, 
*:before, 
*:after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box; 
}
body { margin:0; }
img { border:0; }
.left__align { text-align:left; padding:0 3rem 0 1rem;}
.right__align { text-align:right; padding:0 1rem 0 20vw; }
.Header {
    background-color: #0B0622;
    color: white;
}
.Header .Container {
    background-image: url('../img/sky2.png'), url('../img/sky1.png'), url('../img/sky1.png');
    background-repeat: repeat-x, repeat-x, no-repeat;
    background-position: right, right, left;
    background-size: 12rem, 26rem, 33rem;
     background-position-y: 109%, 117%, 111%;
    background-position-x: 18%, 52%, 0%;
}
.Header .heading_title {
    font-size:8vw;
    line-height:0px;
}
.Container {
    min-height: 100vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-content: space-around;
    overflow: hidden;
    padding-bottom: 30px;
}
.Box {
    align-self: auto;
    flex-basis: 30%;
    flex-grow: 1;
    text-align: center;
    
}
.box_expend_width {
    align-self: flex-start;
    flex-basis: 100%;
}
.heading_title {
    font-size: 3vw;
}

.description_word{
    font-size: 4vw;
    font-weight:bolder;
}
.Description,
.Motto {
    flex-basis: 100%;
    align-self: baseline;
    padding: 0 17vw;
    font-size: 2vw;
}
.picture_box {
    padding: 20px;
    border: 1px solid white;
    margin: 15px;
    border-radius: 5px;
    background-color: #664AE0;
    background-image: linear-gradient(182deg, #6348d9, #486f5fa6);
}
.picture_box img{
    width:60%;
}
.Main .Container:first-child {
    background-image: url('../img/test2.png'), url('../img/test3.png'), url('../img/test4.png');
    background-repeat: no-repeat, no-repeat, repeat-x;
    background-position: right, right, left;
    background-size: 8rem, 26rem, 22rem;
    background-position-y: 53%, 110%, 102%;
    background-position-x: 66vw, 60vw, 0vw;
}
.Main .Container:nth-child(2) {
    background-color: #0F0830;
    color: white;
    padding-left:15px;
    padding-right:15px;
}
.Header .Button{
    padding: 1rem 3rem;
    font-size:2vw;
}
.Button {
    border: 1px solid white;
    background-color: #2B1787;
    padding:15px;
    color:white;
}
.Footer {
    text-align: center;
    border-top: 5px solid #0B0621;
    padding: 15px;
    background-color: #0F0830;
    color: #664AE0;
}

@media screen and (max-width:800px), screen and (max-width:1000px) and (orientation:landscape) {

}
@media screen and (max-width:480px){
    .Header .heading_title {
        font-size: 15vw;
        line-height: initial;
        margin: 0;
    }
    .Box {
        flex-basis: 100%;
    }
    .Description,
    .Motto {
        align-self: auto;
        padding: 0 15px;
        font-size: 7vw;
    }
    .Header .Button {
        display: none;
    }
    .Header .Container {
        background-image: url('../img/sky1.png');
        background-repeat: no-repeat;
        background-position: left;
        background-size: 35rem;
        background-position-y: 102%;
        background-position-x: -7rem;
    }
    .heading_title {
        font-size: 9vw;
        padding: 0 15px;
    }
    .description_word {
        font-size: 8vw;
        text-align: center;
        padding: 0 15px;
    }
    .Main .Container:first-child {
        background-image: url('../img/test4.png');
        background-repeat: repeat-x;
        background-position: left;
        background-size: 22rem;
        background-position-y: 102%;
        background-position-x: 0rem;
    }

}