    width: calc(20% + 100px) -  to calculate some related size to its parent
*/
@charset "utf-8";

.color-title { color:var(--main-bg-color-two); } 
.phone-arrow-down, 
.phone-arrow-up {
    display:none;
}
.tab-arrow-down { transform: rotate(270deg); display: inline-block; margin: 0 30px; }
a {
    text-decoration: none;
    color: white;
    font-weight: bolder;
    display: inline-block;
    padding: 15px;
    transition: ease-out 700ms;
}
a:hover {background-color:var(--main-bg-color-two); color:var(--main-bg-color-one);}
a:active {color:var(--main-bg-color-two); text-decoration:underline;}

nav {
    margin: auto;
    width: 100%;
    background-color: #4A29DB;
}
nav > section{ display:inline-block; }
nav > section:first-child { width:30%; }
nav > section:first-child h1 { text-align:center; color:white; }
nav > section:last-child { width:70%; text-align:right; }

@media screen and (max-width:480px){
    .phone-arrow-up {
        transform: rotate(90deg);
        display: inline-block;
        margin: 0 30px;
    }
    .phone-arrow-down {
        transform: rotate(270deg);
        display: inline-block;
        margin: 0 30px;
    }

    nav { height:100px;overflow:scroll; }
    nav > section{ display:block; }
    nav > section a { display:block; text-align:center; }
    nav > section:first-child { background-color:var(--main-bg-color-one); z-index:1; }
    nav > section:first-child h1 { margin:13px auto; }
    nav > section:first-child,
    nav > section:last-child {
        width: 100%;
        height: 50px;
        border: 1px solid transparent;
    }
    nav > section:first-child { position:fixed; top:0;}
    nav > section:last-child { margin-top:50px; }

}